<template>
  <div class="app-container">
    <header>
      <h1>NBA Stats App</h1>
      <button class="menu-button" @click="toggleMenu">☰</button>
    </header>

    <div class="content">
      <aside :class="{ open: isMenuOpen }">
        <nav>
          <ul>
            <li @click="toggleMenu">Home</li>
            <li @click="toggleMenu">Search Player</li>
            <li @click="toggleMenu">Teams</li>
            <li @click="toggleMenu">About</li>
          </ul>
        </nav>
      </aside>
      
      <main>
        <div class="player-search">
          <input
            v-model="searchQuery"
            @input="searchPlayers"
            @keyup.enter="searchPlayers"
            placeholder="Search for a player"
            @focus="showDropdown = true"
            @blur="hideDropdown"
          />
          <button @click="searchPlayers">Search</button>

          <div v-if="showDropdown && players.length" class="dropdown">
            <ul>
              <li v-for="player in players" :key="player.id" @mousedown="selectPlayer(player)">
                {{ player.full_name }}
              </li>
            </ul>
          </div>

          <div v-if="selectedPlayer">
            <h2>{{ selectedPlayer.full_name }}</h2>

            <div class="table-wrapper">
              <h3>Regular Season Game Logs</h3>
              <table>
                <thead>
                  <tr>
                    <th>Date</th>
                    <th>Matchup</th>
                    <th>WL</th>
                    <th>MIN</th>
                    <th>FGM</th>
                    <th>FGA</th>
                    <th>FG%</th>
                    <th>3PM</th>
                    <th>3PA</th>
                    <th>3P%</th>
                    <th>FTM</th>
                    <th>FTA</th>
                    <th>FT%</th>
                    <th>REB</th>
                    <th>AST</th>
                    <th>STL</th>
                    <th>BLK</th>
                    <th>PTS</th>
                    <th>+/-</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="log in gameLogs" :key="log.game_id">
                    <td>{{ log.game_date }}</td>
                    <td>{{ log.matchup }}</td>
                    <td>{{ log.wl }}</td>
                    <td>{{ log.min }}</td>
                    <td>{{ log.fgm }}</td>
                    <td>{{ log.fga }}</td>
                    <td>{{ log.fg_pct }}</td>
                    <td>{{ log.fg3m }}</td>
                    <td>{{ log.fg3a }}</td>
                    <td>{{ log.fg3_pct }}</td>
                    <td>{{ log.ftm }}</td>
                    <td>{{ log.fta }}</td>
                    <td>{{ log.ft_pct }}</td>
                    <td>{{ log.reb }}</td>
                    <td>{{ log.ast }}</td>
                    <td>{{ log.stl }}</td>
                    <td>{{ log.blk }}</td>
                    <td>{{ log.pts }}</td>
                    <td>{{ log.plus_minus }}</td>
                  </tr>
                </tbody>
              </table>
            </div>

            <div class="table-wrapper">
              <h3>Playoff Game Logs</h3>
              <table>
                <thead>
                  <tr>
                    <th>Date</th>
                    <th>Matchup</th>
                    <th>WL</th>
                    <th>MIN</th>
                    <th>FGM</th>
                    <th>FGA</th>
                    <th>FG%</th>
                    <th>3PM</th>
                    <th>3PA</th>
                    <th>3P%</th>
                    <th>FTM</th>
                    <th>FTA</th>
                    <th>FT%</th>
                    <th>REB</th>
                    <th>AST</th>
                    <th>STL</th>
                    <th>BLK</th>
                    <th>PTS</th>
                    <th>+/-</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="log in playoffLogs" :key="log.game_id">
                    <td>{{ log.game_date }}</td>
                    <td>{{ log.matchup }}</td>
                    <td>{{ log.wl }}</td>
                    <td>{{ log.min }}</td>
                    <td>{{ log.fgm }}</td>
                    <td>{{ log.fga }}</td>
                    <td>{{ log.fg_pct }}</td>
                    <td>{{ log.fg3m }}</td>
                    <td>{{ log.fg3a }}</td>
