                    <td>{{ log.ftm }}</td>
                    <td>{{ log.fta }}</td>
                    <td>{{ log.ft_pct }}</td>
                    <td>{{ log.reb }}</td>
                    <td>{{ log.ast }}</td>
                    <td>{{ log.stl }}</td>
                    <td>{{ log.blk }}</td>
