<script>
import axios from 'axios';

export default {
  data() {
    return {
      searchQuery: '',
      players: [],
      selectedPlayer: null,
      gameLogs: [],
      playoffLogs: [],
      isMenuOpen: false,
      showDropdown: false
    };
  },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
    async searchPlayers() {
      try {
        if (this.searchQuery.trim() === '') {
          this.players = [];
          return;
        }
        console.log(`Searching for players with query: ${this.searchQuery}`);
        const playerResponse = await axios.get(
          `http://localhost:8000/api/players/${this.searchQuery}`
        );
        console.log('Player search response:', playerResponse.data);
        this.players = playerResponse.data;
        this.showDropdown = true;
      } catch (error) {
        console.error('Error fetching players:', error);
        this.players = [];
        this.showDropdown = false;
      }
    },
    selectPlayer(player) {
      this.selectedPlayer = player;
      this.showDropdown = false;
      this.fetchPlayerData(player.id);
    },
    async fetchPlayerData(playerId) {
      try {
        console.log(`Fetching game logs for player ID: ${playerId}`);
        const gameLogsResponse = await axios.get(
          `http://localhost:8000/api/players/${playerId}/games`
        );
        console.log('Game logs response:', gameLogsResponse.data);
        this.gameLogs = gameLogsResponse.data;

        console.log(`Fetching playoff logs for player ID: ${playerId}`);
        const playoffLogsResponse = await axios.get(
          `http://localhost:8000/api/players/${playerId}/playoffstats`
        );
        console.log('Playoff logs response:', playoffLogsResponse.data);
        this.playoffLogs = playoffLogsResponse.data;
      } catch (error) {
        console.error('Error fetching game logs or playoff logs:', error);
        this.gameLogs = [];
        this.playoffLogs = [];
      }
    },
    hideDropdown() {
      setTimeout(() => {
        this.showDropdown = false;
      }, 200);
    }
  }
};
</script>

