<template>
  <div id="app">
    <header>
      <h1>NBA Stats App</h1>
      <button class="menu-button" @click="toggleMenu">☰</button>
    </header>

    <div class="content">
      <aside :class="{ open: isMenuOpen }">
        <nav>
          <ul>
            <li @click="navigateTo('/')">Home</li>
            <li @click="navigateTo('/search')">Search Player</li>
            <li @click="navigateTo('/teams')">Teams</li>
            <li @click="navigateTo('/about')">About</li>
          </ul>
        </nav>
      </aside>
      
      <main>
        <router-view />
      </main>
    </div>

    <footer>
      <p>&copy; 2024 NBA Stats App</p>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isMenuOpen: false
    };
  },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
    navigateTo(route) {
      this.$router.push(route);
      this.toggleMenu();
    }
  }
};
</script>

